@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');


/* ELEMENT SELECTORS */
body{   
     background-image: url('https://github.com/KG-2023/Activity-Images-2023/blob/main/pexels-nikolay-osmachko-230477.jpg?raw=true');
     background-size: cover;
     background-repeat: no-repeat;
     text-align: center;
     font-family: 'Roboto', sans-serif;
    padding: 20px;
}

div{
  background-color: rgb(228,202, 168);
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

h1, h2, p{
  text-align:center;
}

h1{
  font-size: 40px;
  margin: 0;
}

h2{
  font-size: 35px;
}

hr{
  height: .5px;
  background-color: rgb(228, 202, 168, 0.4);
  border-color: rgba(228, 202, 168, 0.406);
  box-shadow: 0px 4px 17px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 0px 4px 17px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 4px 17px rgba(0, 0, 0, 0.75);

}


footer{
  font-size: 14px;
}

a {
  color: black;
  text-decoration: none;
}

/* lvha */
a:link {
  color: #FF0000;
  
}

/* visited link */
a:visited {
  color:#fff;
}

/* mouse over link */
a:hover {
  color: rgb(228, 202, 168);
}

/* selected link */
a:active {
  color: #0000FF;
}
